*/\n\nb,\nstrong {\n  font-weight: bolder;\n}\n\n/**\n * 1. Correct the inheritance and scaling of font size in all browsers.\n * 2. Correct the odd `em` font sizing in all browsers.\n */\n\ncode,\nkbd,\nsamp {\n  font-family: monospace, monospace; /* 1 */\n  font-size: 1em; /* 2 */\n}\n\n/**\n * Add the correct font style in Android 4.3-.\n */\n\ndfn {\n  font-style: italic;\n}\n\n/**\n * Add the correct background and color in IE 9-.\n */\n\nmark {\n  background-color: #ff0;\n  color: #000;\n}\n\n/**\n * Add the correct font size in all browsers.\n */\n\nsmall {\n  font-size: 80%;\n}\n\n/**\n * Prevent `sub` and `sup` elements from affecting the line height in\n * all browsers.\n */\n\nsub,\nsup {\n  font-size: 75%;\n  line-height: 0;\n  position: relative;\n  vertical-align: baseline;\n}\n\nsub {\n  bottom: -0.25em;\n}\n\nsup {\n  top: -0.5em;\n}\n\n/* Embedded content\n   ========================================================================== */\n\n/**\n * Add the correct display in IE 9-.\n */\n\naudio,\nvideo {\n  display: inline-block;\n}\n\n/**\n * Add the correct display in iOS 4-7.\n */\n\naudio:not([controls]) {\n  display: none;\n  height: 0;\n}\n\n/**\n * Remove the border on images inside links in IE 10-.\n */\n\nimg {\n  border-style: none;\n}\n\n/**\n * Hide the overflow in IE.\n */\n\nsvg:not(:root) {\n  overflow: hidden;\n}\n\n/* Forms\n   ========================================================================== */\n\n/**\n * 1. Change the font styles in all browsers (opinionated).\n * 2. Remove the margin in Firefox and Safari.\n */\n\nbutton,\ninput,\noptgroup,\nselect,\ntextarea {\n  font-family: sans-serif; /* 1 */\n  font-size: 100%; /* 1 */\n  line-height: 1.15; /* 1 */\n  margin: 0; /* 2 */\n}\n\n/**\n * Show the overflow in IE.\n * 1. Show the overflow in Edge.\n */\n\nbutton,\ninput { /* 1 */\n  overflow: visible;\n}\n\n/**\n * Remove the inheritance of text transform in Edge, Firefox, and IE.\n * 1. Remove the inheritance of text transform in Firefox.\n */\n\nbutton,\nselect { /* 1 */\n  text-transform: none;\n}\n\n/**\n * 1. Prevent a WebKit bug where (2) destroys native `audio` and `video`\n *    controls in Android 4.\n * 2. Correct the inability to style clickable types in iOS and Safari.\n */\n\nbutton,\nhtml [type=\"button\"], /* 1 */\n[type=\"reset\"],\n[type=\"submit\"] {\n  -webkit-appearance: button; /* 2 */\n}\n\n/**\n * Remove the inner border and padding in Firefox.\n */\n\nbutton::-moz-focus-inner,\n[type=\"button\"]::-moz-focus-inner,\n[type=\"reset\"]::-moz-focus-inner,\n[type=\"submit\"]::-moz-focus-inner {\n  border-style: none;\n  padding: 0;\n}\n\n/**\n * Restore the focus styles unset by the previous rule.\n */\n\nbutton:-moz-focusring,\n[type=\"button\"]:-moz-focusring,\n[type=\"reset\"]:-moz-focusring,\n[type=\"submit\"]:-moz-focusring {\n  outline: 1px dotted ButtonText;\n}\n\n/**\n * Correct the padding in Firefox.\n */\n\nfieldset {\n  padding: 0.35em 0.75em 0.625em;\n}\n\n/**\n * 1. Correct the text wrapping in Edge and IE.\n * 2. Correct the color inheritance from `fieldset` elements in IE.\n * 3. Remove the padding so developers are not caught out when they zero out\n *    `fieldset` elements in all browsers.\n */\n\nlegend {\n  box-sizing: border-box; /* 1 */\n  color: inherit; /* 2 */\n  display: table; /* 1 */\n  max-width: 100%; /* 1 */\n  padding: 0; /* 3 */\n  white-space: normal; /* 1 */\n}\n\n/**\n * 1. Add the correct display in IE 9-.\n * 2. Add the correct vertical alignment in Chrome, Firefox, and Opera.\n */\n\nprogress {\n  display: inline-block; /* 1 */\n  vertical-align: baseline; /* 2 */\n}\n\n/**\n * Remove the default vertical scrollbar in IE.\n */\n\ntextarea {\n  overflow: auto;\n}\n\n/**\n * 1. Add the correct box sizing in IE 10-.\n * 2. Remove the padding in IE 10-.\n */\n\n[type=\"checkbox\"],\n[type=\"radio\"] {\n  box-sizing: border-box; /* 1 */\n  padding: 0; /* 2 */\n}\n\n/**\n * Correct the cursor style of increment and decrement buttons in Chrome.\n */\n\n[type=\"number\"]::-webkit-inner-spin-button,\n[type=\"number\"]::-webkit-outer-spin-button {\n  height: auto;\n}\n\n/**\n * 1. Correct the odd appearance in Chrome and Safari.\n * 2. Correct the outline style in Safari.\n */\n\n[type=\"search\"] {\n  -webkit-appearance: textfield; /* 1 */\n  outline-offset: -2px; /* 2 */\n}\n\n/**\n * Remove the inner padding and cancel buttons in Chrome and Safari on macOS.\n */\n\n[type=\"search\"]::-webkit-search-cancel-button,\n[type=\"search\"]::-webkit-search-decoration {\n  -webkit-appearance: none;\n}\n\n/**\n * 1. Correct the inability to style clickable types in iOS and Safari.\n * 2. Change font properties to `inherit` in Safari.\n */\n\n::-webkit-file-upload-button {\n  -webkit-appearance: button; /* 1 */\n  font: inherit; /* 2 */\n}\n\n/* Interactive\n   ========================================================================== */\n\n/*\n * Add the correct display in IE 9-.\n * 1. Add the correct display in Edge, IE, and Firefox.\n */\n\ndetails, /* 1 */\nmenu {\n  display: block;\n}\n\n/*\n * Add the correct display in all browsers.\n */\n\nsummary {\n  display: list-item;\n}\n\n/* Scripting\n   ========================================================================== */\n\n/**\n * Add the correct display in IE 9-.\n */\n\ncanvas {\n  display: inline-block;\n}\n\n/**\n * Add the correct display in IE.\n */\n\ntemplate {\n  display: none;\n}\n\n/* Hidden\n   ========================================================================== */\n\n/**\n * Add the correct display in IE 10-.\n */\n\n[hidden] {\n  display: none;\n}\n","// normalize.css\n@import \"~normalize.css\";\n\n// surface imports and customization\n// Colors\n@import \"~surface/src/scss/_imports/_colors.scss\";\n$primary: #e67e22;\n$secondary: $asbestos;\n$accent: #ff5722;\n\n@import \"~surface/src/scss/buttons.scss\";\n@import \"~surface/src/scss/form.scss\";\n\n@import 'screen.css';\n\n@import 'github-corner.scss';\n\n::-moz-selection {\n  background: #fff !important;\n  color: #444 !important;\n}\n\n::selection {\n  background: rgba(255, 255, 255, 0) !important;\n  color: #444 !important;\n}\n\n#universe {\n    border: 0;\n    position: fixed;\n    top: 0;\n    left: 0;\n    width: 100%;\n    height: 100%;\n    z-index: -1;\n    cursor: crosshair;\n}\n\n.APL {\n    background: rgba(243, 243, 243, 0.5);\n    font-family: \"APL385\", \"APL385 Unicode\", \"APLX Upright\", \"Courier APL2 Unicode\", \"SImPL\", \"SiMPL medium\", monospace;\n    text-align: center;\n    padding: 0.5em 0;\n    border-radius: 0.5em;\n    // -webkit-text-fill-color: #444;\n    a {\n        text-decoration: none;\n    }\n}\n\n.controls {\n    text-align: center;\n    position: fixed;\n    bottom: 0;\n    margin-left: auto;\n    margin-right: auto;\n    left: 0;\n    right: 0;\n\n    button {\n        margin: 0.5rem 1rem;\n    }\n}\n\n.hidden {\n    display: none;\n}\n\n.github-link {\n    position: fixed;\n    top: 24px;\n    left: 125px;\n    font-size: 1rem;\n    a {\n        text-decoration: none;\n    }\n    .octicon {\n        height: 20px;\n        vertical-align: sub;\n    }\n}\n\n#fps-info {\n    min-width: 10rem;\n    position: fixed;\n    top: 26px;\n    left: 0;\n    right: 80px;\n    text-align: right;\n}\n\n.text-content {\n    position: fixed;\n    top: 55px;\n    bottom: 115px;\n    left: 0;\n    right: 0;\n    max-width: 45rem;\n    overflow: scroll;\n    overflow-x: hidden;\n    margin: auto;\n\n    // nice in FF, broken in chrome\n    // -webkit-text-fill-color: transparent;\n    // background: -webkit-linear-gradient(transparent, #444 5%, #444 95%, transparent);\n    // background: -o-linear-gradient(transparent, #444 5%, #444 95%, transparent);\n    // -webkit-background-clip: text;\n}\n\nsmall {\n    font-size: 0.9rem;\n}","@import './_imports/_variables';\n\n[class^=\"btn\"] {\n\ttransition: all 0.3s $cubic;\n\tbox-shadow: $box-shadow-float;\n\tposition: relative;\n\tcursor: pointer;\n\ttext-transform: uppercase;\n\tmargin-bottom: $space-small;\n\tbackground-image: none;\n\tbackground-size: 0;\n\tbackground-repeat: no-repeat;\n\tbackground-position: 50% 50%;\n\ttransition: background-color 0.3s $cubic, box-shadow 0.3s $cubic;\n\twill-change: background-size, background-image;\n\tpadding: $space-small $space-med;\n\tdisplay: inline-block;\n\tfont-family: Roboto;\n\tborder: 0;\n\t&:after {\n\t\tposition: absolute;\n\t\tcontent: '';\n\t\ttransition: none;\n\t\tbackground: radial-gradient(circle, white 95%, rgba(0,0,0,0) 95%);\n\t\tbackground-size: 0.7%;\n\t\tbackground-position: 50% 50%;\n\t\tbackground-repeat: no-repeat;\n\t}\n\t&:focus {\n\t\toutline: none;\n\t\tbackground-size: 1000%;\n    \ttransition: all 1s $cubic;\n\t}\n}\n\n@mixin btn-gradient($c) {\n\tbackground-image: radial-gradient(circle, $c 1%, lighten($c, 15%) 15%, rgba(0,0,0,0) 30%);\n\tbackground-color: $c;\n}\n\n.btn--float {\n\tborder-radius: 50%;\n\twidth: 50px;\n\theight: 50px;\n\t@include btn-gradient($accent);\n\twill-change: box-shadow, background-color;\n\tfont-size: $font-big;\n\tpadding: 0;\n\t&:hover {\n\t\tbox-shadow: $box-shadow-float-hover;\n\t\tbackground-color: lighten($accent, 5%);\n\t}\n}\n\n.btn--raised {\n\tborder-radius: $border-radius-small*0.66;\n\tbox-shadow: $box-shadow-raised;\n\t@include btn-gradient($river);\n\twill-change: box-shadow, background-color;\n\tcolor: white;\n\t&:hover {\n\t\tbox-shadow: $box-shadow-raised-hover;\n\t\tbackground-color: lighten($river, 5%);\n\t}\n}\n\n.btn--flat {\n\t@include btn-gradient($secondary);\n\tbackground-color: $body-background;\n\tbox-shadow: none;\n}\n\n.btn--primary {\n\t@include btn-gradient($primary);\n\t&:hover {\n\t\tbackground-color: lighten($primary, 5%);\n\t}\n}\n\n.btn--secondary {\n\t@include btn-gradient($secondary);\n\t&:hover {\n\t\tbackground-color: lighten($secondary, 5%);\n\t}\n}\n\n.btn--accent {\n\t@include btn-gradient($accent);\n\t&:hover {\n\t\tbackground-color: lighten($accent, 5%);\n\t}\n}\n\n.btn--red {\n\t@include btn-gradient($alizarin);\n\t&:hover {\n\t\tbackground-color: lighten($alizarin, 5%);\n\t}\n}\n\n.btn--yellow {\n\t@include btn-gradient($sunflower);\n\t&:hover {\n\t\tbackground-color: lighten($sunflower, 5%);\n\t}\n}\n\n.btn--green {\n\t@include btn-gradient($green);\n\t&:hover {\n\t\tbackground-color: lighten($green, 5%);\n\t}\n}\n\n.btn--blue {\n\t@include btn-gradient($blue);\n\t&:hover {\n\t\tbackground-color: lighten($blue, 5%);\n\t}\n}\n\n","@import './colors';\n\n// Type\n$serif: 'Gentium Book Basic', serif !default;\n$sans: Roboto, sans-serif !default;\n$font-small: 12px !default;\n$font-med: 16px !default;\n$font-big: 22px !default;\n$font-huge: 40px !default;\n$line-height: 130% !default;\n\n// Colors\n$primary: $turqoise !default;\n$secondary: lighten($turqoise, 10%) !default;\n$accent: $yellow !default;\n\n$body-background: $off-white !default;\n\n// Grid\n$grid-columns: 12 !default;\n\n// Decorative\n$box-shadow-card: 0 2px 5px 0 rgba(0, 0, 0, 0.1), 0 2px 10px 0 rgba(0, 0, 0, 0.07) !default;\n$box-shadow-float: 0 2px 5px 0 rgba(0, 0, 0, 0.14), 0 2px 10px 0 rgba(0, 0, 0, 0.1) !default;\n$box-shadow-float-hover: 0 5px 11px 0 rgba(0, 0, 0, 0.16), 0 4px 15px 0 rgba(0, 0, 0, 0.13) !default;\n$box-shadow-raised: 0 2px 5px 0 rgba(0, 0, 0, 0.14), 0 2px 10px 0 rgba(0, 0, 0, 0.1) !default;\n$box-shadow-raised-hover: 0 5px 11px 0 rgba(0, 0, 0, 0.16), 0 4px 15px 0 rgba(0, 0, 0, 0.13) !default;\n\n$cubic: cubic-bezier(.64,.09,.08,1) !default;\n\n$border-radius-small: 3px !default;\n$border-radius-med: 6px !default;\n$border-radius-big: 10px !default;\n\n// Spacing\n$space-big: 40px !default;\n$space-med: 20px !default;\n$space-small: 10px !default;\n\n// Media Queries\n$media-med: 1200px !default;\n$media-small: 900px !default;\n$media-tiny: 520px !default;\n","//Greens\n$turqoise: #1abc9c;\n$green-sea: #16a085;\n$emerald: #2ecc71;\n$nephritis: #27ae60;\n$green: #4caf50;\n$light-green: #8bc34a;\n$lime: #cddc39;\n//Blues\n$river: #3498db;\n$belize: #2980b9;\